#map {
  position: absolute;
  width: 100%;
  height: 100%; }
  #map .logo-ci {
    background-color: rgba(255, 255, 255, 0.6);
    position: absolute;
    width: 8em;
    right: 0;
    bottom: 0em;
    z-index: 100;
    padding: 0.25em; }
  #map .ol-zoom {
    position: absolute;
    top: 50%;
    left: 0;
    background-color: transparent; }
    #map .ol-zoom button {
      display: block;
      background-color: #fff;
      border: 2px solid #304968;
      color: #304968;
      margin: 5px;
      border-radius: 50%;
      width: 2rem;
      height: 2rem;
      line-height: 1.5rem;
      font-size: 1.5rem;
      cursor: pointer;
      -webkit-box-shadow: 0px 4px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 12px 1px rgba(0, 0, 0, 0.14), 0px 3px 15px 3px rgba(0, 0, 0, 0.12);
              box-shadow: 0px 4px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 12px 1px rgba(0, 0, 0, 0.14), 0px 3px 15px 3px rgba(0, 0, 0, 0.12); }



